<template>
  <div class="pb-2">
    <label class="col-label bg-color-primary color-color-primary">每日籤運</label>
    <div class="w-100 m-0 p-2 bg-color-primary text-center" v-if="isLogin">
      <!-- ... -->
    </div>
    <div class="w-100 m-0 p-2 bg-color-primary text-center" v-if="!isLogin">
      <a class="btn btn-dos btn-lg btn-block my-2 px-2" href="/login">登入</a>
    </div>

  </div>
</template>

<script>
export default {
  name: "FortunesDaliy",
  props: {
    isLogin: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
        daliy: null,
        fortunes: null,
    };
  },
  mounted() {
    if (this.isLogin) {
      axios
        .get('/api/frontend/fortunes')
        .then((response) => {
          console.log(response.data);
          this.daliy = response.data;
        })
        .catch((error) => console.log(error));
      axios
        .get('/json/fortunes.json')
        .then((response) => {
          this.fortunes = response.data;
        })
        .catch((error) => console.log(error));
    }
  },
  methods: {
  },
};
</script>
